
.ant-menu.ant-menu-dark, .ant-menu-dark .ant-menu-sub, .ant-menu.ant-menu-dark .ant-menu-sub .sider-custom{
  background: #2b2b2b !important;
}

.ant-layout-sider-children {
  border-right: 2px solid #141414;
  background-color: #2b2b2b !important;
}

.ant-menu-dark.ant-menu-dark:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: #3b3b3b;
}

.ant-layout-sider-trigger {
  background: #141414;
}


.header{
  background-color: #2b2b2b !important;
  border-bottom: 2px solid #141414;
  padding: 1em;
}

.header-logo{
  width: 25rem;
}

.footer{
  background-color: #2b2b2b !important;
  border-top: 2px solid #141414;
  padding: 1em;
  color: white;
  height: 48px;
  font-size: 16px;
}

.sidebar-text{
  font-size: 18px;
}
.content-background{
  background-color: white;
}
.dashboard-designs {
  background-color: #efefef;
  padding: 10px;
  border-radius: 8px;
  width: 99%;
  /* min-height: 40vh; */
}
.dashboard-designs-header{
  font-size: 20px;
  height: 24px !important;
}
.dashboard-designs-header-see-all{
  color: rgba(0, 0, 0, 0.85);
}
.dashboard-designs-header-see-all:hover{
  scale: 1.1;
  color: rgba(0, 0, 0, 0.85);
}
.dashboard-designs-spin{
  color: rgba(0, 0, 0, 0.85);
}
.dashboard-designs-card{
  border-radius: 15px;
}
.dashboard{
  max-height: 81vh;
  overflow: scroll;
}
.dashboard-designs-card-image-wrapper{
  height: 350px;
  border-radius: 15px;
}
.dashboard-designs-card-image{
  border-radius: 15px;
  max-height: 345px;
}
.dashboard-designs-oops{
  font-family: 'Patrick Hand', cursive;
}
.ant-btn-primary{
  color: #fff;
  border-color: #2b2b2b !important;
  background: #2b2b2b !important;
  border-radius: 5px;
}
.upload{
  background-color: #efefef;
  padding: 10px;
  border-radius: 8px;
  width: 99%;
  max-height: 81vh;
  overflow: scroll;
}
.status{
  background-color: #efefef;
  padding: 10px;
  border-radius: 8px;
  width: 99%;
  max-height: 81vh;
  overflow: scroll;
}
.status-designs-card{
  border-radius: 15px;
}
.status-designs-card-image-wrapper{
  height: 290px;
  border-radius: 15px;
}
.status-designs-card-image{
  border-radius: 15px;
  max-height: 285px;
}
