.dashboard-designs {
  background-color: #efefef;
  padding: 10px;
  border-radius: 8px;
  width: 99%;
  /* min-height: 40vh; */
}
.dashboard-designs-header{
  font-size: 20px;
  height: 24px !important;
}
.dashboard-designs-header-see-all{
  color: rgba(0, 0, 0, 0.85);
}
.dashboard-designs-header-see-all:hover{
  scale: 1.1;
  color: rgba(0, 0, 0, 0.85);
}
.dashboard-designs-spin{
  color: rgba(0, 0, 0, 0.85);
}
.dashboard-designs-card{
  border-radius: 15px;
}
.dashboard{
  max-height: 81vh;
  overflow: scroll;
}
.dashboard-designs-card-image-wrapper{
  height: 350px;
  border-radius: 15px;
}
.dashboard-designs-card-image{
  border-radius: 15px;
  max-height: 345px;
}
.dashboard-designs-oops{
  font-family: 'Patrick Hand', cursive;
}
.ant-btn-primary{
  color: #fff;
  border-color: #2b2b2b !important;
  background: #2b2b2b !important;
  border-radius: 5px;
}